html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

html, body, #root, .wrapper {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-family: sans-serif; }

ul {
  list-style-type: none; }

a {
  display: inline-block; }
  a:hover {
    color: #fff;
    text-decoration: none; }
  a:visited {
    color: #fff; }
  a:active {
    color: #fff; }

html {
  font-size: 14px;
  color: #fff; }

.body {
  width: 100%;
  height: calc(100% - 50px); }

input, textarea {
  border: 1px solid gainsboro;
  color: #333;
  padding: 5px 10px;
  margin: 5px auto; }
  input:focus, textarea:focus {
    color: #333;
    outline: none; }
  input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
    color: #333; }
  input:-ms-input-placeholder, textarea:-ms-input-placeholder {
    color: #333; }
  input::-ms-input-placeholder, textarea::-ms-input-placeholder {
    color: #333; }
  input::placeholder, textarea::placeholder {
    color: #333; }

button {
  background-color: transparent;
  color: #3F8CBB;
  border: none;
  cursor: pointer; }
  button:focus {
    outline: none; }
  button:hover {
    border-color: #3F8CBB !important; }

.bg-hover-gainsboro:hover {
  background-color: #EAEFF5; }

.bdr-hover-gainsboro:hover {
  border: 1px solid gray !important; }

.block-content {
  background-color: white;
  border-top: 2px solid #3F8CBB;
  border-radius: 2px;
  padding: 15px; }
  .block-content .block-content-title {
    font-size: 2em;
    margin: 15px 0px; }

.mini-block-content {
  background-color: #ebf0f5;
  border-top: 2px solid #3c8dbc;
  border-radius: 2px; }
  .mini-block-content .mini-block-content-title {
    font-size: 1.5em;
    margin: 15px 0px; }

table {
  width: calc(100% - 30px) !important;
  margin: 15px; }

.table td, .table th {
  vertical-align: middle; }



