.block-sidebar-top {
  background-color: #3c8dbc; }
  .block-sidebar-top a, .block-sidebar-top span {
    color: white;
    font-size: 1em;
    line-height: 50px;
    color: white;
    cursor: pointer; }
    .block-sidebar-top a:hover, .block-sidebar-top span:hover {
      background-color: #367fa9; }
  .block-sidebar-top .content-left .logo {
    background-color: #367fa9;
    font-size: 20px;
    width: 230px; }
  .block-sidebar-top .content-right span {
    position: relative; }
    .block-sidebar-top .content-right span sup {
      font-size: 10px;
      padding: 1px 3px;
      position: absolute;
      top: 8px;
      text-align: center;
      vertical-align: middle;
      line-height: 11px;
      border-radius: 3px; }
    .block-sidebar-top .content-right span.icon-mail sup {
      background-color: #00a65a; }
    .block-sidebar-top .content-right span.icon-bell sup {
      background-color: #f39c12; }
    .block-sidebar-top .content-right span.icon-flag sup {
      background-color: #dd4b39; }
  .block-sidebar-top .content-right .user .avatar {
    height: 30px;
    width: 30px;
    background-image: url("../img/avatar.jpg");
    background-size: cover;
    background-position: center center; }
  .block-sidebar-top .content-right .user:hover {
    background-color: #367fa9; }
  .block-sidebar-top .content-right .user .dropdown-menu {
    width: 280px;
    background-color: #3c8dbc;
    left: initial !important;
    top: 100% !important;
    right: 0;
    -webkit-transform: none !important;
        -ms-transform: none !important;
            transform: none !important; }
    .block-sidebar-top .content-right .user .dropdown-menu .user-header .avatar {
      width: 80px;
      height: 80px;
      overflow: hidden;
      border: 3px solid #65A3C9; }
    .block-sidebar-top .content-right .user .dropdown-menu .user-header .user-name {
      font-size: 1.2em; }
      .block-sidebar-top .content-right .user .dropdown-menu .user-header .user-name small {
        font-size: 0.8em; }



